<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`FundsTransferPurpose${dataLayout}`" :key="Date.now()"/>
  </div>
</template>

<script>
import FundsTransferPurposeListLayout from '@/views/ft/funds-transfer-purpose/funds-transfer-purpose-browse/FundsTransferPurposeListLayout'
import FundsTransferPurposeTreeLayout from '@/views/ft/funds-transfer-purpose/funds-transfer-purpose-browse/FundsTransferPurposeTreeLayout'

export default {
  name: 'funds-transfer-purpose-browse',
  components: {
    FundsTransferPurposeListLayout,
    FundsTransferPurposeTreeLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
