<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button class="p-3 mr-4" @click="add" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isFundsTransferAll')">{{ $t('ADD') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="refresh" color="primary" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="expandAll" color="primary" type="filled" icon="fa-plus-square" icon-pack="far">{{ $t('EXPAND_ALL') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="collapseAll" color="primary" type="filled" icon="fa-minus-square" icon-pack="far">{{ $t('COLLAPSE_ALL') }}</vs-button>
      </div>
    </div>
    <div v-if="totalRows === 0">
      <vs-alert active="true" color="warning">
        <i class="far fa-exclamation-circle pr-1"></i> {{ $t('NO_DATA_FOUND') }}
      </vs-alert>
    </div>
    <v-tree v-else ref="tree" :data="treeData" @node-click="nodeClicked"/>
  </nv-card>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import FundsTransferPurposeService from '@/services/ft/FundsTransferPurposeService'
import { VTree } from 'vue-tree-halower'

export default {
  name: 'funds-transfer-purpose-tree-layout',
  mixins: [LoadingMixin],
  components: {
    VTree
  },
  data () {
    return {
      treeData: [],
      totalRows: 0
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      FundsTransferPurposeService.transferPurposeTree({
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.totalRows = response.data.totalRows
        self.treeData = response.data || []
      })
    },
    refresh () {
      this.load()
    },
    table () {
      this.$router.push({ name: 'BrowseFundsTransferPurposes' })
    },
    add () {
      this.$router.push({ name: 'CreateFundsTransferPurpose' })
    },
    nodeClicked (node) {
      this.$router.push({ name: 'ViewFundsTransferPurpose', params: { id: node.data.purposeId } })
    },
    toggleNode (node, expanded) {
      node.expanded = expanded
      if (node.children) {
        for (let i = 0; i < node.children.length; i++) {
          this.toggleNode(node.children[i], expanded)
        }
      }
    },
    expandAll () {
      this.toggleNode(this.treeData[0], true)
    },
    collapseAll () {
      this.toggleNode(this.treeData[0], false)
    }
  },
  mounted () {
    this.load()
  }
}
</script>
